<template>
        <div style="width:200px;height:200px;">
            <highcharts class="chart" :options="chartOptionsGuage"></highcharts>
        </div>
</template>

<script>
    import {
        mapState,
        mapActions
    } from 'vuex'
    import HighchartsVue from "highcharts-vue";
    import Highcharts from "highcharts";
    import highchartsMoreInit from "highcharts/highcharts-more";
    import solidGaugeInit from "highcharts/modules/solid-gauge";

    highchartsMoreInit(Highcharts);
    solidGaugeInit(Highcharts);

    export default {
        props: ["points", "title", "units", "min", "max", "interval", "bands"],
        data() {
            return {
                band1: 10,
                band2: 10,
                band3: 10,
                chartOptionsGuage: {
                        credits: {
                            enabled: false
                            },
                    chart: {
                        backgroundColor: 'transparent',
                        type: 'solidgauge',
                        height: '100%',
                    },

                    exporting: {
                        enabled: false
                    },

                    title: {
                        text: this.title,
                        style: {
                            fontSize: '24px'
                        }
                    },
                    tooltip: {
                        borderWidth: 0,
                        backgroundColor: 'none',
                        shadow: false,
                        style: {
                            fontSize: '16px'
                        },
                        pointFormat: '{series.name}<br><span style="font-size:2em; color: {point.color}; font-weight: bold">{point.y}%</span>',
                        positioner: function(labelWidth) {
                            return {
                                x: (this.chart.chartWidth - labelWidth) / 2,
                                y: (this.chart.plotHeight / 2) + 8
                            };
                        }
                    },

                    pane: {
                        startAngle: 0,
                        endAngle: 360,
                        background: [                           
                            { // Track for Move
                            outerRadius: '112%',
                            innerRadius: '88%',
                            backgroundColor: Highcharts.Color(Highcharts.getOptions().colors[0])
                                .setOpacity(0.3)
                                .get(),
                            borderWidth: 0
                        }, { // Track for Exercise
                            outerRadius: '87%',
                            innerRadius: '63%',
                            backgroundColor: Highcharts.Color(Highcharts.getOptions().colors[1])
                                .setOpacity(0.3)
                                .get(),
                            borderWidth: 0
                        }, { // Track for Stand
                            outerRadius: '62%',
                            innerRadius: '38%',
                            backgroundColor: Highcharts.Color(Highcharts.getOptions().colors[2])
                                .setOpacity(0.3)
                                .get(),
                            borderWidth: 0
                        }]
                    },

                    yAxis: {
                        min: 0,
                        max: 100,
                        lineWidth: 0,
                        tickPositions: []
                    },

                    plotOptions: {
                        series: {
                            dataLabels: {
                                enabled: false,
                                borderRadius: 5,
                                backgroundColor: 'rgba(252, 255, 197, 0.7)',
                                borderWidth: 1,
                                borderColor: '#AAA',
                                y: -6
                            }
                        },
                        solidgauge: {
                            innerRadius: "75%",
                            borderWidth: "100px"
                        }
                    },
                    stockTools: {
                        gui: {
                            className: 'highcharts-bindings-wrapper',
                            enabled: false,
                            toolbarClassName: 'stocktools-toolbar'
                        }
                    },
                    series: [{
                        name: '',
                        data: [{
                            color: Highcharts.getOptions().colors[0],
                            radius: '112%',
                            innerRadius: '88%',
                            y: 0
                        }]
                    }, {
                        name: '',
                        data: [{
                            color: Highcharts.getOptions().colors[1],
                            radius: '87%',
                            innerRadius: '63%',
                            y: 0
                        }]
                    }, {
                        name: '',
                        data: [{
                            color: Highcharts.getOptions().colors[2],
                            radius: '62%',
                            innerRadius: '38%',
                            y: 0
                        }]
                    }]
                }
            };
        },
        watch: {

        },
        methods: {
        calcValue(band, min, max) {
         let value = ((band - min) * 100) / (max - min)
         return value
        },
        bandValues() {
            this.band1 = Math.round(this.calcValue(this.bands, this.interval.cat1.min, this.interval.cat1.max))
            this.band2 = Math.round(this.calcValue(this.bands, this.interval.cat2.min, this.interval.cat2.max))
            this.band3 = Math.round(this.calcValue(this.bands, this.interval.cat3.min, this.interval.cat3.max))
            this.chartOptionsGuage.series[0].data[0].y = this.band1
            this.chartOptionsGuage.series[1].data[0].y = this.band2
            this.chartOptionsGuage.series[2].data[0].y = this.band3
        }
        },
        mounted() {
            this.bandValues()
            console.log(this.bands)
            console.log(this.interval)
         },
    };
</script>

