 <div>

  <div class="text-center">
    <v-menu offset-y>
      <template v-slot:activator="{ on }">
        <v-btn
          color="primary"
          dark
          v-on="on"
        >
          Widgets 
        </v-btn>
      </template>
        
<v-list>
<v-container class="button-position">
 <v-btn v-show="box1 == true"  absolute dark fab right small color="#da1e2d" v-on:click="toggle_widget_1(false), box1 = false">
