        </v-btn>
        <span class="title-button">G1</span>
 <v-btn  v-if="box1 == false" absolute dark fab right small  color="#518b38" v-on:click="toggle_widget_1(true), box1 = true">
            <p class="button-title">on</p>
        </v-btn>
</v-container>

<v-container class="button-position">
 <v-btn v-show="box2 == true"  absolute dark fab right small color="#da1e2d" v-on:click="toggle_widget_2(false), box2 = false">
            <p class="button-title">off</p>
        </v-btn>
        <span class="title-button">G2</span>
 <v-btn  v-if="box2 == false" absolute dark fab right small  color="#518b38" v-on:click="toggle_widget_2(true), box2 = true">
            <p class="button-title">on</p>
        </v-btn>
</v-container>

<v-container class="button-position">
 <v-btn v-show="box3 == true"  absolute dark fab right small color="#da1e2d" v-on:click="toggle_widget_3(false), box3 = false">
            <p class="button-title">off</p>
        </v-btn>
        <span class="title-button">G3</span>
 <v-btn  v-if="box3 == false" absolute dark fab right small  color="#518b38" v-on:click="toggle_widget_3(true), box3 = true">
            <p class="button-title">on</p>
        </v-btn>
</v-container>
</v-list>
    </v-menu>
  </div>

 
