          }
        ]
      }
    };
  },

  computed: {
    ...mapState({
      ohlc: state => state.bitmex.ohlc,
      simpleAveragePoints: state => state.bitmex.simpleAveragePoints,
      lowerLiquidationLeverage1: state => state.bitmex.lowerLiquidationLeverage1,
      lowerLiquidationLeverage2: state => state.bitmex.lowerLiquidationLeverage2,
      lowerLiquidationLeverage3: state => state.bitmex.lowerLiquidationLeverage3,
      upperLiquidationLeverage1: state => state.bitmex.upperLiquidationLeverage1,
      upperLiquidationLeverage2: state => state.bitmex.upperLiquidationLeverage2,
      upperLiquidationLeverage3: state => state.bitmex.upperLiquidationLeverage3,
      forceKeyRender: state => state.bitmex.ForceRenderChart,
      updateLineCharts: state => state.bitmex.updateLineCharts
    })
  },

  watch : {
    forceKeyRender: function() {
      this.destroyLines()
      this.setLeverageLines(true)
    }, 
    updateLineCharts: function() {
     this.setLeverageLines(false, true)
    }
  },
  methods: {
    ...mapActions({
      setBitmexData: VUEX_GET_REQUEST_OHLC,
      applyLiquidationOptions: VUEX_SET_APPLY_LIQUIDATIONS_OPTIONS,
      getUserSettingsAndLiquidations: VUEX_GET_USER_SETTINGS
    }),

    resize() {
      let toolBarCompensate =
        screen.width == document.body.clientWidth ? 0 : 64;

      this.$refs.chart.chart.update({
        chart: {
          height: window.innerHeight - toolBarCompensate,
          width: window.innerWidth
        }
      });
    },
    getLeverage(position, price, leverage) {
      let mmr = 0.5 / 100;
      let result = 0
      let lev = leverage
      let avgPrice = price
      if (position == "LONG") {
        result = (avgPrice * lev) / (lev + 1 - mmr * lev);
      } else {
        result = (avgPrice * lev) / (lev - 1 + mmr * lev);
      }
      return +result.toFixed(2);
    },
    destroyLines() {
        for (let i=1; i<=14; i++ ) {
        this.chartOptions.series[i].data = []
        }
    },
    setLeverageLines(destroyed, updated) {
        let simpleAveragePoints = JSON.parse(JSON.stringify(this.simpleAveragePoints))

        let that = this
        if (simpleAveragePoints == undefined) {
          return
        }
        if (that.initialSet == false || destroyed == true || updated == true) {
        if (simpleAveragePoints.length == 0) {
            return
          }
        if (updated == true) {
          for (let i=1; i<=14; i++){
              that.chartOptions.series[i].data[that.chartOptions.series[i].data.length-1].dataLabels.enabled = false
            }
          }
        let dataLabelEnabled = false;
        for (let i in simpleAveragePoints) {
          if (i == simpleAveragePoints.length - 1) {
            dataLabelEnabled = true;
          }
          let timestamp =  simpleAveragePoints[i].updated
          let finalBuy = simpleAveragePoints[i].final_buy_sa
          let finalSell = simpleAveragePoints[i].final_sell_sa
          that.chartOptions.series[1].data.push({
            y: parseFloat(finalBuy.toFixed(2)),
            dataLabels: { enabled: dataLabelEnabled, x:-100, style: { fontSize: '16px', fontWeight: "bold" }},
            x:timestamp
          }
          );
          that.chartOptions.series[2].data.push({
            y: finalSell,
            x: timestamp,
            dataLabels: { enabled: false }
            }
          );
          that.chartOptions.series[3].data.push({
            y: that.getLeverage(
              "LONG",
              finalBuy,
              that.lowerLiquidationLeverage1
            ),
            x: timestamp,
            dataLabels: { enabled: dataLabelEnabled,  x:-100,style: { fontSize: '16px', fontWeight: "bold" }}
          });
          that.chartOptions.series[4].data.push({
            y: that.getLeverage(
              "SHORT",
              finalBuy,
              that.upperLiquidationLeverage1
            ),
            x: timestamp,
            dataLabels: { enabled: dataLabelEnabled,  x:-100,style: { fontSize: '16px', fontWeight: "bold" }}
          });
          that.chartOptions.series[5].data.push({
            y: that.getLeverage(
              "LONG",
              finalSell,
              that.lowerLiquidationLeverage1
            ),
            x: timestamp,
            dataLabels: { enabled: dataLabelEnabled, x:-100, style: { fontWeight: "bold" }}
          });
          that.chartOptions.series[6].data.push({
            y: that.getLeverage(
              "SHORT",
              finalSell,
              that.upperLiquidationLeverage1
            ),
            x: timestamp,
            dataLabels: { enabled: dataLabelEnabled, x:-100, style: { fontSize: '16px', fontWeight: "bold" }}
          });
          that.chartOptions.series[7].data.push({
            y: that.getLeverage(
              "LONG",
              finalBuy,
              that.lowerLiquidationLeverage2
            ),
            x: timestamp,
            dataLabels: { enabled: dataLabelEnabled,  x:-100, style: { fontSize: '16px', fontWeight: "bold" }}
          });
          that.chartOptions.series[8].data.push({
            y: that.getLeverage(
              "SHORT",
              finalBuy,
              that.upperLiquidationLeverage2
            ),
            x: timestamp,
            dataLabels: { enabled: dataLabelEnabled, x:-100, style: { fontSize: '16px', fontWeight: "bold" }}
          });
          that.chartOptions.series[9].data.push({
            y: that.getLeverage(
              "LONG",
              finalSell,
              that.lowerLiquidationLeverage2
            ),
            x:timestamp,
            dataLabels: { enabled: dataLabelEnabled,  x:-100, style: { fontSize: '16px', fontWeight: "bold" }}
          });
          that.chartOptions.series[10].data.push({
            y: that.getLeverage(
              "SHORT",
